export const COMPONENT_STANDARDS_VERSION = '1.0.0';

// =============================================================================
// ÂàùÂßãÂåñÂáΩÊï∞
// =============================================================================

/**
 * ÂàùÂßãÂåñÁªÑ‰ª∂Ê†áÂáÜÂ∫ì
 * ËÆæÁΩÆÂÖ®Â±ÄÈÖçÁΩÆÂíåÈªòËÆ§ÂÄº
 */
export function initializeComponentStandards(config?: {
  theme?: 'light' | 'dark' | 'auto';
  accessibility?: {
    enableValidation?: boolean;
    enableAnnouncements?: boolean;
  };
  performance?: {
    enableMonitoring?: boolean;
    enableOptimizations?: boolean;
  };
  development?: {
    enableWarnings?: boolean;
    enableMetrics?: boolean;
  };
}) {
  const defaultConfig = {
    theme: 'auto' as const,
    accessibility: {
      enableValidation: process.env.NODE_ENV === 'development',
      enableAnnouncements: true
    },
    performance: {
      enableMonitoring: process.env.NODE_ENV === 'development',
      enableOptimizations: true
    },
    development: {
      enableWarnings: process.env.NODE_ENV === 'development',
      enableMetrics: process.env.NODE_ENV === 'development'
    }
  };
  
  const finalConfig = {
    ...defaultConfig,
    ...config,
    accessibility: {
      ...defaultConfig.accessibility,
      ...config?.accessibility
    },
    performance: {
      ...defaultConfig.performance,
      ...config?.performance
    },
    development: {
      ...defaultConfig.development,
      ...config?.development
    }
  };
  
  // ËÆæÁΩÆÂÖ®Â±ÄÈÖçÁΩÆ
  if (typeof window !== 'undefined') {
    (window as any).__COMPONENT_STANDARDS_CONFIG__ = finalConfig;
  }
  
  // ÂºÄÂèëÁéØÂ¢ÉÊèêÁ§∫
  if (finalConfig.development.enableWarnings) {
    console.log(
      `üé® ÁªÑ‰ª∂Ê†áÂáÜÂ∫ìÂ∑≤ÂàùÂßãÂåñ (v${COMPONENT_STANDARDS_VERSION})`,
      finalConfig
    );
  }
  
  return finalConfig;
}

/**
 * Ëé∑ÂèñÂΩìÂâçÈÖçÁΩÆ
 */
export function getComponentStandardsConfig() {
  if (typeof window !== 'undefined') {
    return (window as any).__COMPONENT_STANDARDS_CONFIG__ || null;
  }
  return null;
}

